.text {
    width: 100%;
    height: auto;
    padding-top: 10px;
    padding-bottom: 20px;
    background-color: var(--color-whitesmoke-300);
}
.shareSvgrepocom {
    display: flex;
    z-index: 999;
    width: 95%;
    margin-left: 13px;
    height: 90px;
    background-color: var(--color-whitesmoke-100);
    margin-top: 10px;
    border-radius: 10px;
}
.grifriend {
    position: relative;
    left: 10px;
    top: 10px;
}
.smithanayar {
    margin-left: 20px;
    margin-top: -10px;
}

.paragraph {
    font-size: 12px;
    margin-left: 20px;
    margin-top: -20px;
} 

.buttonconfirmrequest {
    z-index: 999;
    margin-left: 100px;
}

.confirm {
    font-size: 14px;
    padding: 2px 20px;
    margin-top: 10px;
    margin-left: 6px;
    border-radius: 10px;
    cursor: pointer;
    background-color: var(--color-whitesmoke-300);
    border: 0;
}

.Massage {  
    font-size: 15px; 
    margin-left: 20px;
    margin-top: 40px;
    border-radius: 10px;
    cursor: pointer;
    background-color: var(--color-whitesmoke-300);
    padding: 2px 20px;
    cursor: pointer;
}


.modalpopup {
    width: 400px;
    height: 200px;
    margin-top: 80px;
    /* margin-left: -200px; */
    position: relative;
    left: -120px;
    background-color: var(--color-whitesmoke-300);
    justify-content: center;
    z-index: 999;
}
 
.Friendrequest {
    z-index: 999;
    width: 90%;
    margin-left: 19px;
    height: 6vh;
    position: relative; 
    row-gap: 20px;
    text-align: center;
    border-radius: 10px;
    background-color: rgba(226, 219, 217, 0.5);
    box-shadow: 0 4px 14px rgba(0, 0, 0, 0.1);
    transition: all .5s ease-in-out;
    border: 1px solid black;
}

.friend {
    margin-top: -1px;
    font-weight: 200;
}

.confirmFriendrequest {
    z-index: 999;
    width: 90%;
    margin-left: 19px;
    height: 6vh;
    position: relative; 
    row-gap: 20px;
    text-align: center;
    font-size: medium;
    margin-top: 50px;
}

.yes {
    border-radius: var(--br-mini);
    background-color: var(--color-whitesmoke-100);
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    overflow: hidden;
    font-size: larger;
    display: flex;
    margin-top: 5px;
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    padding: 4px;
    box-sizing: border-box;
    min-width: 112px;
    cursor: pointer;
    font-family: var(--font-poppins);
    font-size: var(--font-size-base);
}

.buttons {
    display: flex;
    margin-left: 70px;
    gap: 20px;
}