.login,
.login1 {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
}
.login1 {
  margin-top: -16122px;
  margin-left: -1193px;
  flex: 1;
  border-radius: var(--br-mini);
  background-color: var(--color-whitesmoke-200);
  overflow: hidden;
  align-items: center;
  justify-content: center;
  padding: var(--padding-98xl) var(--padding-xl) var(--padding-96xl)
    var(--padding-4xl);
  max-width: 100%;
}
.login {
  margin-top: -8177px;
  width: 1728px;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-7224xl);
  max-width: 306%;
  flex-shrink: 0;
}
.image58Icon {
  height: 122px;
  flex: 1;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  object-fit: cover;
  cursor: pointer;
}
.image58Wrapper {
  width: 446px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-2xl) var(--padding-18xl);
  box-sizing: border-box;
  max-width: 100%;
  margin-top: 200px;
}
.confirmThatYou {
  height: 66px;
  width: 401px;
  position: relative;
  letter-spacing: 0.13em;
  font-weight: 500;
  display: inline-block;
  flex-shrink: 0;
  max-width: 100%;
}
.googleSignInWarning {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 var(--padding-lgi) var(--padding-10xl);
  box-sizing: border-box;
  max-width: 100%;
  flex-shrink: 0;
}
.policyAndTerms {
  color: var(--color-royalblue);
  text-decoration: none;
}
.toCreateYourContainer {
  height: 139px;
  width: 455px;
  position: relative;
  letter-spacing: 0.13em;
  display: inline-block;
  flex-shrink: 0;
  max-width: 100%;
}
.createAccountHint {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 var(--padding-xl) 0 var(--padding-30xl);
  box-sizing: border-box;
  max-width: 100%;
  flex-shrink: 0;
  font-size: var(--font-size-smi);
}
.cancel {
  height: 18px;
  width: 117px;
  position: relative;
  letter-spacing: 0.13em;
  font-weight: 500;
  display: inline-block;
  flex-shrink: 0;
}
.cancelConfirmFrame {
  flex: 1;
  border-radius: var(--br-mini);
  background-color: var(--color-white);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-3xs) var(--padding-3xs) var(--padding-mid);
  box-sizing: border-box;
  min-width: 112px;
  cursor: pointer;
}
.confirm {
  height: 18px;
  width: 117px;
  position: relative;
  letter-spacing: 0.13em;
  font-weight: 500;
  display: inline-block;
  flex-shrink: 0;
}
.cancelConfirmFrame1 {
  flex: 1;
  border-radius: var(--br-mini);
  background-color: var(--color-white);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: var(--padding-5xs) var(--padding-5xs) var(--padding-lgi);
  box-sizing: border-box;
  min-width: 112px;
  cursor: pointer;
}
.cancelFrame,
.loginFrame,
.logoRemoveBgPreview {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  max-width: 100%;
  /* margin-top: 100px; */
}
.loginFrame {
  flex: 1;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0 55px;
}
.cancelFrame,
.logoRemoveBgPreview {
  width: 430px;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-10xl);
  box-sizing: border-box;
}
.logoRemoveBgPreview {
  height: 885px;
  width: 593px;
  border-radius: var(--br-11xl);
  background-color: var(--color-whitesmoke-300);
  border: 7px solid var(--color-black);
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: column;
  align-items: center;
  padding: 0 var(--padding-10xl) var(--padding-207xl) 0;
  gap: 16px 0;
  z-index: 1;
  text-align: center;
  font-size: var(--font-size-lg);
  color: var(--color-black);
  font-family: var(--font-poppins);
}
@media screen and (max-width: 1200px) {
  .login,
  .logoRemoveBgPreview {
    padding-bottom: var(--padding-1970xl);
    box-sizing: border-box;
  }
  .logoRemoveBgPreview {
    padding-bottom: var(--padding-43xl);
  }
}
@media screen and (max-width: 1050px) {
  .login1 {
    padding-top: var(--padding-106xl);
    padding-bottom: var(--padding-56xl);
    box-sizing: border-box;
  }
  .login,
  .logoRemoveBgPreview {
    padding-bottom: var(--padding-1274xl);
    box-sizing: border-box;
  }
  .logoRemoveBgPreview {
    padding-bottom: var(--padding-21xl);
  }
}
@media screen and (max-width: 750px) {
  .createAccountHint {
    padding-left: var(--padding-5xl);
    box-sizing: border-box;
  }
  .logoRemoveBgPreview {
    height: auto;
  }
}
@media screen and (max-width: 450px) {
  .login,
  .login1 {
    padding-bottom: var(--padding-30xl);
    box-sizing: border-box;
  }
  .login {
    padding-bottom: var(--padding-821xl);
  }
  .loginFrame {
    gap: 0 55px;
  }
  .logoRemoveBgPreview {
    padding-bottom: var(--padding-7xl);
    box-sizing: border-box;
  }
}
