.selectLanguage {
  position: absolute;
  top: 0;
  left: 0;
  font-weight: 500;
  display: none;
}
.listboxTitle {
  width: 280px; 
  height: 15.6px;
  position: relative;
  display: none;
  z-index: 1;
}
.div,
.item1 {
  align-self: stretch;
}
.div {
  width: 60px;
  position: relative;
  display: inline-block;
}
.item1 {
  margin-top: -89px;
  height: 44px;
  background-color: var(--color-white);
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-base);
  box-sizing: border-box;
}
.div1 {
  width: 55px;
  position: relative;
  display: inline-block;
}
.div1,
.div2,
.item2 {
  align-self: stretch;
}
.item2 {
  margin-top: -44px;
  height: 44px;
  background-color: var(--color-white);
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-base);
  box-sizing: border-box;
}
.div2 {
  width: 57px;
  position: relative;
  display: inline-block;
}
.item3 {
  margin-bottom: -44px;
  height: 44px;
  background-color: var(--color-white);
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-base);
  box-sizing: border-box;
}
.div3,
.item3,
.item4 {
  align-self: stretch;
}
.div3 {
  width: 63px;
  position: relative;
  display: inline-block;
}
.item4 {
  margin-bottom: -89px;
  height: 44px;
  background-color: var(--color-white);
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-xs) var(--padding-base);
  box-sizing: border-box;
}
.clipList,
.dropdownList {
  flex: 1;
  overflow: hidden;
  display: flex;
  align-items: flex-start;
}
.dropdownList {
  height: 1px;
  border-radius: var(--br-5xs);
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.1);
  flex-direction: column;
  justify-content: flex-start;
  gap: 1px 0;
  opacity: 0;
}
.clipList {
  align-self: stretch;
  flex-direction: row;
  justify-content: center;
  padding: var(--padding-49xl) var(--padding-sm);
  z-index: 2;
}
.listboxbg {
  height: 100%;
  width: 100%;
  position: absolute;
  margin: 0 !important;
  top: -0.2px;
  right: 0;
  bottom: 0.2px;
  left: 0;
  border-radius: var(--br-5xs);
  background-color: var(--color-white);
  box-shadow: 0 4px 14px rgba(0, 0, 0, 0.1);
  border: 1px solid var(--color-black);
  box-sizing: border-box;
}
.language {
  margin-bottom: -5px;
  position: relative;
}
.languageWrapper,
.placeholderText {
  display: flex;
  justify-content: flex-start;
}
.languageWrapper {
  height: 15px;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
  box-sizing: border-box;
}
.placeholderText {
  align-self: stretch;
  width: 184px;
  flex-direction: row;
  align-items: center;
  z-index: 999;
}
.chevronIcon {
  height: 3.9px;
  width: 10px;
  position: relative;
  z-index: 1;
}
.listboxComponent,
.listboxMain {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  margin-top: 100px;
}
.listboxMain {
  width: 280px;
  height: 47px;
  flex-direction: row;
  justify-content: space-between;
  padding: var(--padding-base) var(--padding-3xl) var(--padding-mini)
    var(--padding-base);
  position: relative;
  gap: var(--gap-xl);
  margin-top: -46px;
  color: var(--color-dimgray-100);
}
.listboxComponent {
  margin-top: 3px;
  width: 308px;
  height: 126px;
  /* flex-direction: column; */
  justify-content: flex-start;
  padding: var(--padding-2xl) 0 var(--padding-12xs);
  text-align: left;
  font-size: var(--font-size-base);
  color: var(--color-darkslategray-200);
  font-family: var(--font-montserrat);
}
