.text {
    width: 68%;
    /* height: 100vh; */
    background-color: white;
    margin-top: 10px;
    border-radius: 20px;
}

.border {
    width: 60%;
    /* height: 80vh; */
    margin-left: 100px;
    padding: 50px;
}

.AccountSection {
    display: flex;
    margin-top: 20px;
}

.Account{
    color: rgb(0, 0, 0);
} 

.logout {
    color: black;
    font-size: 15px;
    padding: 3px 35px;
    margin-top: 10px;
    background-color: #BDC8F1;
    border-radius: 20px;
    border: none;
    margin-left: 55%;
    cursor: pointer;
}

.Accountdetailsection {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0 auto; 
}

.Accountdetails p {
    font-size: 1.2rem;
    font-weight: 100;
    padding: 6px;
    color: black;
    background-color: #BDC8F1;
    border: 1px solid #BDC8F1;
}

.AccountEmail {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: -20px;
    padding-left: 32px;
    padding-right: 30px;
        border: 1px solid #BDC8F1;
}

.email {
    font-size: 1.1rem;
    color: black;
}

.AccountPassword {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 32px;
    padding-right: 30px;
    border: 1px solid #BDC8F1;
}

.password {
    font-size: 1.1rem;
    color: black;
}

.editIcon img {
    width: 15px;
    height: 15px; 
    cursor: pointer;
}

/* Styles for logout container */
.logoutforalldevice {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border: 1px solid #BDC8F1;

}

/* Styles for logout button */
.buttonforlogout {
    color: #2e2f31;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.buttonforlogout p {
    margin: 0;
    font-size: 18px;
}





.settings_container {
    display: flex;
    flex-direction: column;
    margin-top: 40px;
  }
  
  .settings_item {
    border: 1px solid #BDC8F1;
    color: black;
    font-size: 15px;
    padding: 10px;

  }
  
  .settings_item1 {
    border: 1px solid #BDC8F1;
    color: black;  
    font-size: 15px;
    background-color: #BDC8F1;
    padding: 10px;

  }
  .settings_title {
    font-size: 18px;
    color: black;
    padding: 1px;
  }
  
  .settings_description {
    font-size: 14px;
    color: black;

  }
 
  