.messageChild,
.messageItem {
  position: absolute;
  border-radius: var(--br-11xs);
}
.messageChild {
  height: 58.33%;
  width: 75%;
  top: 20.83%;
  right: 12.5%;
  bottom: 20.83%;
  left: 12.5%;
  border: 2px solid var(--color-darkslategray-100);
  box-sizing: border-box;
}
.messageItem {
  height: 16.67%;
  width: 66.67%;
  top: 37.5%;
  right: 16.67%;
  bottom: 45.83%;
  left: 16.67%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.message {
  width: 24px;
  height: 24px;
  position: relative;
  display: none;
  z-index: 1;
}
.loginChild {
  width: 100%;
  height: 410px;
  position: absolute;
  margin: 0 !important;
  right: 0;
  bottom: -33px;
  left: 0;
  background-color: var(--color-lightsteelblue-200);
  z-index: 2;
}
.login,
.loginFrame {
  display: flex;
  box-sizing: border-box;
}
.loginFrame {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  max-width: 100%;
  margin-top: -105px;
}
.login {
  width: 100%;
  position: relative;
  border-radius: var(--br-mini);
  background-color: var(--color-white);
  overflow: hidden;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 0 var(--padding-6xs) var(--padding-96xl) var(--padding-3xs);
  letter-spacing: normal;
}


@media screen and (max-width: 1200px) {
  .loginChild {
    display: none;
  }
}




.mt {
  margin-top: 30px;
  margin-left: 50px;
  gap: 20px;
  padding: 10px;
}