.rECTANGLE {
  width: 100%;
  height: 410px;
  position: absolute;
  margin: 0 !important;
  right: 0;
  bottom: -33px;
  left: 0;
  background-color: var(--color-lightsteelblue-200);
}
.listBoxComponentInstance {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  box-sizing: border-box;
  max-width: 100%;
  flex-shrink: 0;
  margin-top: -105px;
}
.loginUsingFacebook {
  width: 100%;
  position: relative;
  border-radius: var(--br-mini);
  background-color: var(--color-white);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 0 var(--padding-6xs) var(--padding-96xl) var(--padding-3xs);
  box-sizing: border-box;
  letter-spacing: normal;
}
