.massage {
    z-index: 999;
    width: 100%;
    height: 400px;
    border-radius: 10px;
    background-color: rgb(255, 255, 255);
}

.textSearchFriends {
    width: 90%;
    height: 30px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
    border: 1px solid rgb(0, 0, 0);
    border-radius: 20px;
    margin-top: 20px;
    margin-left: 10px;
}
.searchFriends {
    width: calc(100% - 5.5px);
    border: 0px solid rgb(224, 211, 211);
    outline: 0;
    font-weight: 500;
    font-family: var(--font-poppins);
    font-size: var(--font-size-lg);
    background-color: transparent;
    height: 15px;
    flex: 1;
    position: relative;
    left: 10px;
    letter-spacing: 0.13em;
    color: var(--color-black);
    text-align: left;
    display: inline-block;
    min-width: 205px;
    max-width: 100%;
}

.massageProfile {
    padding: 10px;    
}

.Jeremy {
    margin-top: 30px;
    font-size: 14px;
    display: flex;
}

.JeremyName {
    margin-left: 20px;
    margin-top: 10px;
}

.activeButton {
    height: 13px;
    margin-top: 10px;
    display: flex;
    margin-left: 30px;
}

.active {
    margin-top: -3px;
    margin-left: 10px;
}