.listbox {
  width: 100%;
  height: 410px;
  position: absolute;
  margin: 0 !important;
  right: 0;
  bottom: -3px;
  left: 0;
  background-color: var(--color-lightsteelblue-200);
}
.logo1RemovebgPreview4Icon {
  width: 144px;
  height: 145px;
  position: relative;
  object-fit: cover;
}
.forgotPassword {
  margin: 0;
  align-self: stretch;
  position: relative;
  font-size: inherit;
  letter-spacing: 0.13em;
  font-weight: 400;
  font-family: inherit;
}
.enterTheEmail {
  width: 333px;
  height: 24px;
  position: relative;
  font-size: var(--font-size-smi);
  letter-spacing: 0.12em;
  text-align: center;
  display: inline-block;
  flex-shrink: 0;
  max-width: 100%;
}
.forgotPasswordParent,
.rectangle {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  max-width: 100%;
}
.forgotPasswordParent {
  align-self: stretch;
  gap: 17px 0;
}
.rectangle {
  width: 351px;
  gap: 32px 0;
}
.enterEmail {
  width: 175px;
  border: 0;
  outline: 0;
  font-family: var(--font-poppins);
  font-size: var(--font-size-base);
  background-color: transparent;
  height: 23px;
  position: relative;
  letter-spacing: 0.03em;
  color: var(--color-gray-200);
  text-align: left;
  display: inline-block;
}
.emailInput {
  align-self: stretch;
  border-radius: var(--br-3xs);
  background-color: var(--color-white);
  box-shadow: 8px 9px 7.2px 3px rgba(0, 0, 0, 0.25);
  border: 1px solid var(--color-black);
  overflow: hidden;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  padding: var(--padding-6xs) var(--padding-7xl) var(--padding-2xs);
}
.checkRingLightIcon {
  height: 20px;
  width: 21px;
  position: relative;
}
.submit {
  position: relative;
  font-size: var(--font-size-lg);
  letter-spacing: 0.03em;
  font-family: var(--font-poppins);
  color: #3b3b3b;
  text-align: left;
}
.checkmarkLight,
.signUpText {
  display: flex;
  flex-direction: row;
  margin-left: 19%;
  margin-top: 50px;
  box-shadow: #3b3b3b;
}
.signUpText {
  cursor: pointer;
  border: 1px solid var(--color-black);
  padding: var(--padding-4xs) var(--padding-8xs) var(--padding-8xs);
  background-color: #abb3dd;
  width: 169px;
  border-radius: var(--br-mini);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
  overflow: hidden;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
}
.signUpText:hover {
  background-color: #7880ab;
  border: 1px solid var(--color-darkslategray-200);
  box-sizing: border-box;
}
.checkmarkLight {
  width: 284px;
  align-items: flex-end;
  justify-content: space-between;
  gap: var(--gap-xl);
}
.signUp {
  color: #2b41b4;
}
.dontHaveAnContainer {
  margin-bottom: -9px;
  height: 48px;
  position: relative;
  letter-spacing: 0.03em;
  display: inline-block;
  flex-shrink: 0;
}
.buttonCheck,
.noAccountMessage {
  flex: 1;
  display: flex;
  justify-content: flex-start;
}
.noAccountMessage {
  height: 39px;
  flex-direction: column;
  align-items: flex-start;
  padding: 0;
  box-sizing: border-box;
}
.buttonCheck {
  overflow: hidden;
  flex-direction: row;
  align-items: flex-end;
  padding: var(--padding-smi) var(--padding-10xl) 0 var(--padding-sm);
}
.textInputEmailChild {
  height: 20px;
  width: 299px;
  position: relative;
  background-color: var(--color-gray-100);
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.framePasswd,
.textInputEmail {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  max-width: 100%;
}
.textInputEmail {
  flex: 1;
  overflow: hidden;
  align-items: center;
  justify-content: flex-end;
  padding: var(--padding-7xs) var(--padding-9xs) var(--padding-8xs)
    var(--padding-lgi);
  cursor: pointer;
  z-index: 1;
}
.framePasswd {
  align-self: stretch;
  align-items: flex-start;
  justify-content: flex-start;
  padding: 0 0 0 var(--padding-14xl);
}
.submitButton {
  flex: 1;
  flex-direction: column;
  gap: 39px 0;
}
.checkboxFrame,
.submitButton,
.vector {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  max-width: 100%;
}
.checkboxFrame {
  width: 402px;
  flex-direction: row;
  padding: 0 var(--padding-sm);
  box-sizing: border-box;
}
.vector {
  width: 435px;
  flex-direction: column;
  gap: 47px 0;
  font-size: var(--font-size-base);
}
.forgotPasswordFrame,
.rectangleParent {
  display: flex;
  box-sizing: border-box;
  max-width: 100%;
}
.rectangleParent {
  width: 593px;
  border-radius: var(--br-11xl);
  background-color: var(--color-whitesmoke-300);
  border: 7px solid var(--color-black);
  overflow: hidden;
  flex-shrink: 0;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 98px 72px 206px;
  gap: 82px 0;
  z-index: 1;
}
.forgotPasswordFrame {
  align-self: stretch;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  padding: 0 var(--padding-xl);
  margin-top: -114px;
  text-align: left;
  font-size: var(--font-size-15xl);
  color: var(--color-black);
  font-family: var(--font-poppins);
}
.fogotPassword {
  width: 100%;
  position: relative;
  border-radius: var(--br-mini);
  background-color: var(--color-whitesmoke-200);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: flex-start;
  padding: 0 var(--padding-6xs) 121px var(--padding-5xs);
  box-sizing: border-box;
  letter-spacing: normal;
}
@media screen and (max-width: 900px) {
  .forgotPassword {
    font-size: var(--font-size-8xl);
  }
  .rectangleParent {
    padding-top: var(--padding-115xl);
    padding-bottom: var(--padding-115xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 675px) {
  .rectangleParent {
    gap: 82px 0;
    padding-left: var(--padding-17xl);
    padding-right: var(--padding-17xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 450px) {
  .forgotPassword {
    font-size: var(--font-size-xl);
  }
  .rectangle {
    gap: 32px 0;
  }
  .submitButton {
    gap: 39px 0;
  }
  .vector {
    gap: 47px 0;
  }
  .rectangleParent {
    gap: 82px 0;
    padding-bottom: 87px;
    box-sizing: border-box;
  }
}
